html {
  width:100vw;
  overflow-x:hidden;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F3F7FA;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-webkit-keyframes fill {
  from {
    width: 0;
  }

  to {
    width: 100%;
  }
}

@keyframes fill {
  from {
    width: 0;
  }

  to {
    width: 100%;
  }
}

@-webkit-keyframes fly {
  from {
    right: 100%;
  }

  to {
    right: -8px;
  }
}

@keyframes fly {
  from {
    right: 100%;
  }

  to {
    right: -8px;
  }
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.df {
  display: flex;
}

.jcsb {
  justify-content: space-between;
}

.tal {
  text-align: left;
}

.tar {
  text-align: right;
}

.main {
  width: 1024px;
  padding: 160px 100px 100px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  position: relative;
}

.logo {
  position: absolute;
  left: 50%;
  top: 80px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.sidebar {
  box-shadow: 0 1px 4px rgba(91, 137, 164, 0.25);
  border-radius: 5px;
  width: 232px;
  padding: 15px 0;
  background-color: #fff;
}

.sidebar > * {
  margin-bottom: 30px;
}

.sidebar > *:last-child {
  margin-bottom: 0;
}

.sidebar-title {
  color: #4a4a4a;
  font-size: 12px;
  font-weight: 600;
  line-height: 19px;
  text-transform: uppercase;
  margin-bottom: 10px;
}
.currency {
  padding: 0 15px;
}

.currency-container {
  display: flex;
  align-items: center;
  overflow: hidden;
}

.currency-btn {
  transition: all .3s;
  cursor: pointer;
  height: 40px;
  line-height: 40px;
  width: 100%;
  text-align: center;
  color: #2196f3;
  font-size: 12px;
  font-weight: 600;
  border: 1px solid #d2d5d6;
}

.currency-btn:first-child {
  border-radius: 5px 0 0 5px;
  margin-right: -1px;
}

.currency-btn:last-child {
  border-radius: 0 5px 5px 0;
  margin-left: -1px;
}

.currency-btn.active {
  position: relative;
  color: #fff;
  background-color: #2196f3;
  border-color: #2196f3;
}

.currency-btn:not(.active):hover {
  position: relative;
  z-index: 1;
  border: 1px solid #64b5f5;
  background-color: #f2fcff;
}
.stops-title {
  padding: 0 15px;
}

.stops-item {
  position: relative;
  height: 36px;
  line-height: 36px;
  padding: 0 15px;
}

.stops-item:hover {
  background-color: #f1fcff;
}

.stops-item:hover .stops-only {
  display: block;
}

.stops-item input {
  position: absolute;
  left: -20px;
  display: none;
}

.stops-label {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 30px;
  transition: all .3s;
  cursor: pointer;
  position: relative;
}

.stops-label:not([for='all']) {
  height: 100%;
  line-height: 18px;
}

.stops-label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 19px;
  height: 19px;
  border-radius: 3px;
  border: 1px solid #d2d5d6;
}

.stops-item input:checked + .stops-label:before {
  border: 1px solid #2196f3;
}

.stops-item input:checked + .stops-label:after {
  content: '';
  display: block;
  width: 3px;
  height: 7px;
  border-width: 2px;
  border-style: solid;
  border-color: transparent #3e9ce8 #3e9ce8 transparent;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  position: absolute;
  left: 7px;
  top: 11px;
}

.stops-only {
  display: none;
  position: absolute;
  right: 15px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  color: #2196f3;
  font-size: 11px;
  font-weight: 600;
  text-transform: uppercase;
  cursor: pointer;
}

.stops-from {
  font-size: 12px;
  line-height: 1;
  color: #8b9497;
}
.tickets-container {
  width: 566px;
}
.ticket {
  height: 161px;
  display: flex;
  margin-bottom: 20px;
  transition: box-shadow .3s;
  box-shadow: 0 1px 4px rgba(91, 137, 164, 0.25);
  border-radius: 5px;
  background-color: #fff;
}

.ticket:hover {
  box-shadow: 0 10px 50px rgba(91, 137, 164, 0.25);
}

.ticket-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-width: 200px;
  width: 200px;
  border-right: 1px solid #ECEFF1;
}

.ticket-main img {
  width: 120px;
  margin-bottom: 20px;
}

.ticket-main a {
  transition: all .3s;
  text-align: center;
  width: calc(100% - 40px);
  background-color: #ff6d00;
  border-radius: 5px;
  box-shadow: 0 2px 1px rgba(0, 0, 0, 0.1), 0 1px 0 #d64d08;
  color: #fff;
  text-decoration: none;
  padding: 6px;
  font-size: 16px;
  font-weight: 600;
}

.ticket-main a:hover {
  background-color: #FF8124;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.25), 0 1px 0 #f7661d;
}

.ticket-main a span {
  display: block;
}

.ticket-details {
  padding: 26px;
  width: 100%;
}

.ticket-time {
  font-size: 32px;
  color: #4a4a4a;
  align-items: flex-start;
}

.ticket-city,
.ticket-date {
  font-size: 12px;
}

.ticket-city {
  color: #4a4a4a;
  line-height: 18px;
  font-weight: 600;
}

.ticket-date {
  color: #8b9497;
}

.ticket-stops {
  color: #8b9497;
  font-size: 10px;
  line-height: 12px;
  font-weight: 600;
  width: 96px;
  position: relative;
  text-align: center;
  text-transform: uppercase;
}

.ticket-stops:before {
  content: '';
  position: absolute;
  left: -8px;
  bottom: -4px;
  width: 0;
  height: 1px;
  background-color: #d2d5d6;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-name: fill;
          animation-name: fill;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.ticket-stops:after {
  content: '';
  position: absolute;
  right: 100%;
  bottom: -9px;
  width: 13px;
  height: 13px;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  background-image: url(/plane.svg);
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-name: fly;
          animation-name: fly;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@media (max-width: 767.98px) {
  .main {
    width: calc(566px + 40px);
    padding: 160px 20px 40px;
    flex-direction: column;
  }

  .sidebar {
    width: 100%;
    margin-bottom: 20px;
  }

  .stops-container {
    display: flex;
    flex-wrap: wrap;
  }

  .stops-item {
    padding-right: 65px;
  }

  .stops-only {
    right: 8px;
  }
}
